import { WatchlistDataTablePriceCell } from "../../WatchlistDataTableCells/WatchlistDataTablePriceCell"
import { genericColumn } from "../../genericColumn"
import { getBalanceSheetFiscalPeriod } from "../../utils/fiscalPeriodUtils"

function interestDebtPerShareTtm(tableName: string) {
  const title = "Interest Debt/Share (LTM)"
  return genericColumn({
    endpoint: "companyRatios",
    columnKeys: ["interestDebtPerShareTtm"],
    size: 110,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "Interest debt per share over the last 12 months.",
    cell: info => {
      return (
        <WatchlistDataTablePriceCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyRatios}
          onClick={() => {
            const fiscalPeriod = getBalanceSheetFiscalPeriod(info.row.original)
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} reported in ${fiscalPeriod} about interest paid on debt?`,
              value: info.row.original.financialData.companyRatios?.data?.interestDebtPerShareTtm,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={info.row.original.financialData.companyRatios?.data?.interestDebtPerShareTtm}
        />
      )
    },
  })
}

export { interestDebtPerShareTtm }
